<template>
  <div>
    <div class="leavingmessage">
      <input
        type="text"
        id="leavingmessagetitle"
        placeholder="您的小标题"
        v-model="leavingmessageTitlevalue"
      >
    </div>
    <div class="leavingmessage">
      <!-- <textarea type="text" id="leavingmessage" placeholder="感谢您的使用，使用过程有任何建议都可以反馈给我们，我们会尽快给您解决" value="" v-model="leavingmessagevalue"></textarea> -->
      <textarea
        type="text"
        id="leavingmessage"
        placeholder="感谢您的使用，使用过程有任何建议都可以反馈给我们，我们会尽快给您解决"
        v-model="leavingmessagevalue"
      ></textarea>
    </div>
    <!--  -->
    <div class="leavingmessage">
      <input
        type="tel"
        name
        id="leavingmessageinput"
        placeholder="请留下您的联系方式"
        v-model="leavingmessageIphonevalue"
      >
    </div>
    <div class="leavingmessage">
      <mu-row>
        <mu-col span="8" offset="2">
          <mu-button color="primary" full-width @click="leavingmessageSubmit()">提交</mu-button>
        </mu-col>
      </mu-row>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      msg: "hello 数据详情",
      leavingmessageTitlevalue: null,
      leavingmessagevalue: null,
      leavingmessageIphonevalue: null,
      // Url: "http://192.168.50.151:8800/system/feedback/add",
      Url: "http://47.112.16.168:8800/system/feedback/add",
      token: sessionStorage.getItem("token"),
      username: localStorage.getItem("username")
    };
  },
  methods: {
    leavingmessageSubmit() {
      var that = this;
      var content = this.leavingmessagevalue;
      var phone = this.leavingmessageIphonevalue;
      var subject = this.leavingmessageTitlevalue;
      var username = this.username;
      if (
        content == null ||
        phone == null ||
        subject == null ||
        username == null
      ) {
        this.$toast.warning("请完善反馈信息！！！");
      } else {
        this.$http({
          method: "POST",
          url: that.Url,
          headers: {
            token: that.token
          },
          data: {
            content,
            phone,
            subject,
            username
          }
        })
          .then(res => {
            console.log(res);
            if (res.data.message=="上传成功") {
                that.$router.replace('/Home');
                this.$toast.success(res.data.message);
            } else {
                this.$toast.error(res.data.message);
            }
             
          })
          .catch(err => {
            console.log(err);
          });
      }
    }
  }
};
</script>
<style scoped>
#leavingmessageinput {
  width: 100%;
  border: none;
  height: 35px;
  outline: none;
}
#leavingmessage {
  width: 100%;
  border: none;
  height: 200px;
  outline: none;
}
.leavingmessage {
  padding: 8px;
  border: none;
}
#leavingmessagetitle {
  width: 100%;
  border: none;
  height: 35px;
  outline: none;
}
</style>



